	module.exports = {"data":{"markdownRemark":{"html":"<p>In JavaScript, when the <code>+</code> operator is used with a String value, it is called the <code>concatenation</code> operator. You can build a new string out of other strings by concatenating them together.</p>\n<h2>Example</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code>    <span class=\"token string\">'My name is Alan,'</span> <span class=\"token operator\">+</span> <span class=\"token string\">' I concatenate.'</span>\n</code></pre>\n      </div>\n<h2>Note</h2>\n<p>Watch out for spaces. Concatenation does not add spaces between concatenated strings, so you’ll need to add them yourself.</p>","fields":{"slug":"/certificates/concatenating-strings-with-plus-operator/"},"frontmatter":{"title":"Concatenating Strings with Plus Operator"}}},"pathContext":{"slug":"/certificates/concatenating-strings-with-plus-operator/"}}

